import _forEach from 'lodash/forEach';

const state = {
  onlineLogin: '',
  planningName: '',
  planningNumber: '',
  zipcode: '',
  planInStore: '',
  assembly: '',
  dateArrangementDay: '',
  dateArrangementTime: '',
  desiredDate: '',
  roomMeasurement: '',
  ordernumber: '',
  title: '',
  firstname: '',
  lastname: '',
  street: '',
  housenumber: '',
  addressAddition: '',
  city: '',
  email: '',
  phone: '',
  secondPhone: '',
  privacyChecked: false,
  validSections: {}
};

const getters = {
  getZipcode: state => state.zipcode,
  isFormularValid: state => {
    let valid = true;
    _forEach(state.validSections, value => {
      if (!value) {
        valid = false;
        return false;
      }
    });
    return valid;
  }
};

const mutations = {
  validSectionsAdd: (state, payload) => {
    state.validSections = { ...state.validSections, [payload]: false };
  },
  validSectionsRemove: (state, payload) => {
    delete state.validSections[payload];
  },
  validSectionsUpdateSection: (state, payloadObj) => {
    state.validSections = Object.assign(state.validSections, payloadObj);
  },
  updateOnlineLogin: (state, payload) => {
    state.onlineLogin = payload;
  },
  updatePlanningName: (state, payload) => {
    state.planningName = payload;
  },
  updatePlanningNumber: (state, payload) => {
    state.planningNumber = payload;
  },
  updateZipcode: (state, payload) => {
    state.zipcode = payload;
  },
  updatePlanInStore: (state, payload) => {
    state.planInStore = payload;
  },
  updateAssembly: (state, payload) => {
    state.assembly = payload;
  },
  updateDateArrangementDay: (state, payload) => {
    state.dateArrangementDay = payload;
  },
  updateDateArrangementTime: (state, payload) => {
    state.dateArrangementTime = payload;
  },
  updateDesiredDate: (state, payload) => {
    state.desiredDate = payload;
  },
  updateRoomMeasurement: (state, payload) => {
    state.roomMeasurement = payload;
  },
  updateOrdernumber: (state, payload) => {
    state.ordernumber = payload;
  },
  updateTitle: (state, payload) => {
    state.title = payload;
  },
  updateFirstname: (state, payload) => {
    state.firstname = payload;
  },
  updateLastname: (state, payload) => {
    state.lastname = payload;
  },
  updateStreet: (state, payload) => {
    state.street = payload;
  },
  updateHousenumber: (state, payload) => {
    state.housenumber = payload;
  },
  updateAddressAddition: (state, payload) => {
    state.addressAddition = payload;
  },
  updateCity: (state, payload) => {
    state.city = payload;
  },
  updateEmail: (state, payload) => {
    state.email = payload;
  },
  updatePhone: (state, payload) => {
    state.phone = payload;
  },
  updateSecondPhone: (state, payload) => {
    state.secondPhone = payload;
  },
  updatePrivacyChecked: (state, payload) => {
    state.privacyChecked = payload;
  }
};

export default {
  state,
  getters,
  mutations
};
