<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="date-arrangement-section">
        <h6>Wann können wir dich zur Terminabsprache erreichen?</h6>
        <form-field
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'dateArrangementDay-mofr',
                name: 'dateArrangementDay',
                text: 'Montag bis Freitag',
                value: 'mo-fr'
              },
              {
                id: 'dateArrangementDay-sa',
                name: 'dateArrangementDay',
                text: 'Samstag',
                value: 'sa'
              }
            ]
          }"
          @change="dateArrangementDay = $event;"
        ></form-field>

        <form-field
          :children="{
            childType: 'dropdown',
            id: 'id',
            label: 'Uhrzeit',
            disabled: isDateArrangementTimeActive,
            dropdownItems: [
              {
                name: '',
                value: '',
                disabled: true
              },
              {
                name: 'von 8 bis 12 Uhr',
                value: '8-12'
              },
              {
                name: 'von 12 bis 16 Uhr',
                value: '12-16'
              },
              {
                name: 'von 16 bis 20 Uhr',
                value: '16-20'
              }
            ]
          }"
          :shouldValidate="startDateArrangementValidate"
          :valid="isDateArrangementValid"
          @change="dateArrangementTime = $event;"
          :error="{
            msg: 'Bitte wähle einen Zeitpunkt zur Terminabsprache aus.'
          }"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import { mapGetters } from 'vuex';

export default {
  components: {
    FormField
  },
  data() {
    return {
      dateArrangementDay: null,
      dateArrangementTime: null,
      startDateArrangementValidate: false
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'dateArrangement');
  },
  beforeDestroy() {
    this.$store.commit('updateDateArrangementDay', '');
    this.$store.commit('updateDateArrangementTime', '');
    this.$store.commit('validSectionsRemove', 'dateArrangement');
  },
  watch: {
    getIsGlobalFormValidation() {
      this.startDateArrangementValidate = true;
    },
    dateArrangementDay() {
      this.$store.commit('updateDateArrangementDay', this.dateArrangementDay);
    },
    dateArrangementTime() {
      this.$store.commit('updateDateArrangementTime', this.dateArrangementTime);
    },
    isDateArrangementValid() {
      this.$store.commit('validSectionsUpdateSection', {
        dateArrangement: this.isDateArrangementValid
      });
    }
  },
  computed: {
    isDateArrangementTimeActive() {
      return this.dateArrangementDay === null;
    },
    isDateArrangementDayValid() {
      return this.dateArrangementDay !== null;
    },
    isDateArrangementTimeValid() {
      return this.dateArrangementTime !== null;
    },
    isDateArrangementValid() {
      return this.isDateArrangementDayValid && this.isDateArrangementTimeValid;
    },
    ...mapGetters(['getIsGlobalFormValidation'])
  },
  methods: {
    // ...mapMutations([''])
  }
};
</script>
