<template>
  <div class="calendar" :class="{ isLoading: isLoading }">
    <div
      @click="openCalendar"
      class="fake-input"
      :class="{ disabled: isDisabled }"
    >
      <div class="form-field">
        <div
          class="input-wrap input-wrap--leading-icon"
          :class="{ 'input-wrap--labelled': fakeInputText !== '' }"
        >
          <input
            :disabled="isDisabled"
            id="wunschtermin"
            maxlength="255"
            name="wunschtermin"
            type="text"
            :value="fakeInputText"
          />
          <label for="wunschtermin" class="">Dein Wunschtermin</label>
          <svg class="svg-icon black" v-if="!isLoading">
            <use xlink:href="#calendar-24"></use>
          </svg>
          <loading-spinner v-if="isLoading"></loading-spinner>
        </div>
      </div>
    </div>

    <light-box
      :visible="showCalendar"
      modalType="alert"
      :light="false"
      class="calendar-lightbox"
    >
      <div
        class="calendar"
        v-if="showCalendar"
        :style="freezedCalendarSizeStyleObj"
      >
        <vue-resize-sensor
          @resize="calendarSize = $event;"
          class="resizer"
        ></vue-resize-sensor>

        <div class="calendar-top">
          <div class="calendar-nav">
            <ikea-button
              type="icon"
              icon="arrow-left-24"

              @click="gotoPrevWeek"
            ></ikea-button>
            <div class="calendar-header-text" v-html="calenderHeaderText"></div>
            <ikea-button
              type="icon"
              icon="arrow-right-24"
              @click="gotoNextWeek"
            ></ikea-button>
            <ikea-button
              type="icon"
              icon="cross-24"
              @click="closeCalendar"
            ></ikea-button>
          </div>
        </div>

        <div class="calendar-content" v-if="!isLoading && calendarData !== []">
          <div class="calendar-scroll-wrapper" v-bar>
            <div class="calendar-scroll">
              <div
                v-for="(day, key) in calendarData"
                :key="key"
                class="calendar-day"
              >
                <p class="calendar-day-title">
                  <b>{{ getCalendarDayTitle(key) }}</b>
                </p>
                <div class="calendar-timeslots" v-if="day.length > 0">
                  <template v-for="(timeslot, key) in day">
                    <ikea-button
                      type="outlined"
                      :text="getTimeSlotButtonText(timeslot)"
                      @click="reserveTimeslot(timeslot);"
                      :key="key"
                      class="calendar-day-button"
                    ></ikea-button>
                  </template>
                </div>
                <p class="no-timeslots-text" v-if="day.length === 0">
                  Keine Zeiten verfügbar.
                </p>
              </div>
            </div>
          </div>
        </div>

        <div class="calendar-content-loading" v-if="isLoading">
          <loading-spinner></loading-spinner>
        </div>
      </div>
    </light-box>

    <inline-alert
      v-if="isError"
      type="error"
      title="Es tut uns leid."
      message="Es ist ein Fehler aufgetreten. Bitte versuche es zu einem späteren Zeitpunkt noch einmal."
      icon="error-inverted"
      version="expressive"
      size="m"
    ></inline-alert>

    <inline-alert
      v-if="isNoTimeslotError"
      type="info"
      title="Es tut uns leid."
      message="Momentan sind leider alle Termine vergeben. Bitte versuche es zu einem späteren Zeitpunkt noch einmal."
      icon="info"
      version="subtle"
      size="m"
    ></inline-alert>
  </div>
</template>

<script>
import VueResizeSensor from 'vueresizesensor';
import { mapState } from 'vuex';
import LoadingSpinner from 'styleguide/packages/loading-spinner';
import InlineAlert from 'styleguide/packages/inline-alert';
import Button from 'styleguide/packages/button';
import LightBox from 'styleguide/packages/lightbox';
import moment from 'moment';
import _forEach from 'lodash/forEach';
import _sortBy from 'lodash/sortBy';

export default {
  components: {
    [VueResizeSensor.name]: VueResizeSensor,
    LoadingSpinner,
    InlineAlert,
    LightBox,
    'ikea-button': Button
  },
  data() {
    return {
      showCalendar: false,
      calendarTime: null,
      showModal: false,
      calendarSize: {
        width: 0,
        height: 0
      },
      freezeModalSize: false,
      freezedCalendarSize: null,
      recursiveFetch: false,
      recursiveFetchTimes: 0,
      recursiveFetchTimesMax: 4,
      isNoTimeslotError: false
    };
  },
  beforeDestroy() {
    if (this.$store.getters.hasReservation) {
      this.cancelReservation(this.$store.state.calendar.reservation);
    }
    this.$store.commit('unsetError');
  },
  watch: {
    showCalendar() {
      let $body = document.getElementsByTagName('body')[0];
      let $html = document.getElementsByTagName('html')[0];
      if (this.showCalendar) {
        $body.style.overflow = 'hidden';
        $html.style.overflow = 'hidden';
      } else {
        $body.style.removeProperty('overflow');
        $html.style.removeProperty('overflow');
      }
    },
    freezeModalSize() {
      // eslint-disable-next-line
      this.freezedCalendarSize = this.freezeModalSize ? this.calendarSize : null;
    },
    isDisabled() {
      if (this.isDisabled) {
        this.isNoTimeslotError = false;
        if (this.$store.getters.hasReservation) {
          this.cancelReservation(this.$store.state.calendar.reservation);
        }
      }
    },
    zipcode() {
      if (this.$store.getters.hasReservation) {
        this.cancelReservation(this.$store.state.calendar.reservation);
      }
    },
    planInStore() {
      if (this.$store.getters.hasReservation) {
        this.cancelReservation(this.$store.state.calendar.reservation);
      }
    }
  },
  computed: {
    freezedCalendarSizeStyleObj() {
      if (this.freezedCalendarSize !== null) {
        return {
          width: this.freezedCalendarSize.w + 'px',
          height: this.freezedCalendarSize.h + 'px'
        };
      }
    },
    isDisabled() {
      return (
        this.$store.state.formularData.desiredDate !== 'calendar' &&
        !this.$store.getters.isPlanInStore &&
        this.$store.state.isInstantAnswer
      );
    },
    fakeInputText() {
      if (this.$store.state.calendar.timeslot === null) {
        return '';
      } else {
        return (
          moment(this.$store.state.calendar.timeslot.start).format(
            'dd. DD.MM. HH:mm – '
          ) + moment(this.$store.state.calendar.timeslot.end).format('HH:mm')
        );
      }
    },
    isLoading() {
      return this.$store.state.calendar.isLoading;
    },
    isReservationTimer() {
      return this.$store.state.calendar.isReservationTimer;
    },
    calenderWeek() {
      return moment(this.calendarTime).format('w');
    },
    calenderYear() {
      return moment(this.calendarTime).format('YYYY');
    },
    calenderHeaderText() {
      // eslint-disable-next-line
      let firstDayOfWeek = moment(this.calendarTime).startOf('week').format('DD.MM.');
      // eslint-disable-next-line
      let lastDayOfWeek = moment(this.calendarTime).endOf('week').format('DD.MM.YYYY');
      let calendarWeek = moment(this.calendarTime).format('w');
      return (
        firstDayOfWeek +
        ' – ' +
        lastDayOfWeek +
        ' <nobr>(KW ' +
        calendarWeek +
        ')</nobr>'
      );
    },
    calendarData() {
      if (this.calendarTime !== null) {
        // filter timeslots for the next bookable day
        let bookableTimeslots = [];
        _forEach(this.$store.state.calendar.timeslots, value => {
          if (moment(value.start).isAfter(moment().add(30, 'minutes'))) {
            bookableTimeslots.push(value);
          }
        });
        // sort by start date
        let data = _sortBy(bookableTimeslots, 'start');
        let days = {};
        // iterate from monday to saturday
        for (let i = 0; i < 6; i++) {
          let weekday = moment(this.calendarTime)
            .startOf('week')
            .add(i, 'days')
            .format();
          if (days[weekday] === undefined) {
            days[weekday] = [];
          }
        }
        // push timeslots into days
        _forEach(data, value => {
          let weekday = moment(value.start)
            .startOf('day')
            .format();
          if (days[weekday] !== undefined) {
            days[weekday].push(value);
          }
        });
        return days;
      } else {
        return {};
      }
    },
    isCalendarDataEmpty() {
      let isEmpty = true;
      _forEach(this.calendarData, value => {
        if (value.length > 0) {
          isEmpty = false;
          return false;
        }
      });
      return isEmpty;
    },
    ...mapState({
      isError: state => state.calendar.isError,
      error: state => state.calendar.error,
      zipcode: state => state.formularData.zipcode,
      planInStore: state => state.formularData.planInStore
    })
  },
  methods: {
    openCalendar() {
      if (!this.isDisabled) {
        this.isNoTimeslotError = false;
        if (this.calendarTime === null) {
          this.recursiveFetch = true;
        }
        this.$store.commit('unsetError');
        if (this.$store.getters.hasReservation) {
          this.cancelReservation(this.$store.state.calendar.reservation).then(
            () => {
              this.setCalendarTimeAndOpen();
            }
          );
        } else {
          this.setCalendarTimeAndOpen();
        }
      }
    },
    setCalendarTimeAndOpen() {
      this.calendarTime = moment().format();
      this.fetchCalendarData();
      this.showCalendar = true;
    },
    fetchCalendarData() {
      this.$store.dispatch('getAuthToken').then(() => {
        if (!this.isError) {
          this.$store
            .dispatch('getFreeTimeSlotsByWeek', {
              year: this.calenderYear,
              week: this.calenderWeek
            })
            .then(() => {
              if (!this.isError) {
                if (
                  this.recursiveFetch &&
                  this.isCalendarDataEmpty &&
                  this.recursiveFetchTimes < this.recursiveFetchTimesMax
                ) {
                  this.gotoNextWeek();
                  this.recursiveFetchTimes += 1;
                } else if (
                  this.recursiveFetchTimes >= this.recursiveFetchTimesMax
                ) {
                  this.recursiveFetch = false;
                  this.recursiveFetchTimes = 0;
                  this.isNoTimeslotError = true;
                  this.closeCalendar();
                } else {
                  this.recursiveFetch = false;
                  this.freezeModalSize = false;
                }
              }
            });
        } else {
          this.closeCalendar();
        }
      });
    },
    closeCalendar() {
      this.showCalendar = false;
    },
    gotoPrevWeek() {
      this.freezeModalSize = true;
      this.calendarTime = moment(this.calendarTime)
        .subtract(1, 'week')
        .format();
      this.fetchCalendarData();
    },
    gotoNextWeek() {
      this.freezeModalSize = true;
      this.calendarTime = moment(this.calendarTime)
        .add(1, 'week')
        .format();
      this.fetchCalendarData();
    },
    reserveTimeslot(timeslot) {
      this.$store.dispatch('getAuthToken').then(() => {
        if (!this.isError) {
          this.$store
            .dispatch('reserve', {
              timeslot: timeslot
            })
            .then(() => {
              if (!this.isError) {
                this.closeCalendar();
              }
            });
        }
      });
    },
    cancelReservation(reservation) {
      return new Promise((resolve, reject) => {
        this.$store.dispatch('getAuthToken').then(() => {
          if (!this.isError) {
            this.$store
              .dispatch('cancel', {
                reservation: reservation
              })
              .then(() => {
                if (!this.isError) {
                  resolve();
                } else {
                  reject();
                }
              });
          } else {
            reject();
          }
        });
      });
    },
    getTimeSlotButtonText(timeslot) {
      return (
        moment(timeslot.start).format('HH:mm') +
        ' - ' +
        moment(timeslot.end).format('HH:mm')
      );
    },
    getCalendarDayTitle(key) {
      return moment(key).format('dddd DD.MM.');
    }
  }
};
</script>
