import formularData from './formularData';

const state = {
  showLoading: false,
  isMainSectionValid: false,
  isAssembly: false,
  isRemainingFormular: false,
  isGlobalFormValidation: false
};

const getters = {
  submitType: (state, getters) => {
    if (
      getters.isZipcodeFromSpecialStore &&
      formularData.state.planInStore === 'yes'
    ) {
      return 'STORE';
    } else if (formularData.state.desiredDate === 'immediately') {
      return 'IMMEDIATELY';
    } else if (formularData.state.assembly === true) {
      return 'WITH_ASSEMBLY';
    } else {
      return 'WITHOUT_ASSEMBLY';
    }
  },
  showPlanInStore: (state, getters) => {
    return state.isMainSectionValid && getters.isZipcodeFromSpecialStore;
  },
  showAssembly: (state, getters) => {
    if (
      state.isMainSectionValid &&
      getters.isZipcodeFromSpecialStore &&
      formularData.state.planInStore === 'no'
    ) {
      return true;
    } else if (state.isMainSectionValid && !getters.isZipcodeFromSpecialStore) {
      return true;
    }
    return false;
  },
  showDateArrangement: () => {
    return formularData.state.assembly === true;
  },
  showDateCalendar: (state, getters) => {
    if (getters.isPlanInStore) {
      return true;
    } else if (formularData.state.assembly === false) {
      return true;
    }
    return false;
  },
  showRemainingFormular: state => {
    return state.isRemainingFormular;
  },
  isPlanInStore: () => {
    return formularData.state.planInStore === 'yes';
  },
  getIsGlobalFormValidation: state => state.isGlobalFormValidation
};

const mutations = {
  setIsRemainingFormularTrue: state => {
    // happened only once
    state.isRemainingFormular = true;
  },
  setIsMainSectionValidTrue: state => {
    state.isMainSectionValid = true;
  },
  updateGlobalFormValidation: (state, payload) => {
    state.isGlobalFormValidation = payload;
  },
  startLoading: state => {
    state.showLoading = true;
  },
  stopLoading: state => {
    state.showLoading = false;
  }
};

const actions = {
  // TODO: Better rerun function of start global validation...
  startGlobalFormValidation: context => {
    context.commit('updateGlobalFormValidation', false);
    setTimeout(() => {
      context.commit('updateGlobalFormValidation', true);
    }, 50);
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
