import axios from 'axios';
import _cloneDeep from 'lodash/cloneDeep';
import _merge from 'lodash/merge';
import familynumber from './familynumber';

const state = {
  defaultTrackingData: {
    event_category: 'local_tracking_user_actions',
    event_action: '',
    event_label: '',
    custom: {
      site_language: 'de',
      site_market: 'de',
      site_platform: 'owfe',
      page_type: 'local pages',
      page_category: 'ikea kitchens (pilot)',
      query_parameter: window.location.search,
      user_agent: navigator.userAgent,
      local_dimension180:
        process.env.NODE_ENV === 'production' ? 'prod' : 'dev',
      local_dimension182: window.location.href,
      local_dimension185: 'expert check'
    }
  },
  buildDate: '',
  gaClientId: 'clientId'
};
const mutations = {
  trackingSetBuildDate(state, payload) {
    state.buildDate = payload;
  },
  trackingSetGaClientId(state, payload) {
    state.gaClientId = payload;
  }
};

const actions = {
  initialTrack(store) {
    store.dispatch('getBuildDate').then(() => {
      store.dispatch('getGaClientId').then(() => {
        const payload = _cloneDeep(store.state.defaultTrackingData);
        _merge(payload, {
          event_action: 'signup_begun_kitchen_expert-check',
          custom: {
            ga_client_id: store.state.gaClientId,
            local_dimension181: store.state.buildDate
          }
        });
        if (process.env.NODE_ENV !== 'production') {
          // eslint-disable-next-line
          console.log('track', payload);
        }
        window.sendEvent(payload);
      });
    });
  },
  finalTrack(store) {
    store.dispatch('getBuildDate').then(() => {
      store.dispatch('getGaClientId').then(() => {
        const payload = _cloneDeep(store.state.defaultTrackingData);
        _merge(payload, {
          event_action: 'signup_completed_kitchen_expert-check',
          custom: {
            ga_client_id: store.state.gaClientId,
            local_dimension181: store.state.buildDate,
            local_dimension186: familynumber.state.cuco
          }
        });
        if (process.env.NODE_ENV !== 'production') {
          // eslint-disable-next-line
          console.log('track', payload);
        }
        window.sendEvent(payload);
      });
    });
  },
  getBuildDate(store) {
    if (store.state.buildDate !== '') {
      return store.state.buildDate;
    }
    return axios
      .get('version.json')
      .then(response => {
        store.commit('trackingSetBuildDate', response.data.version.buildDate);
        return response.data.version.buildDate;
      })
      .catch(error => {
        // eslint-disable-next-line
        console.log(error);
      });
  },
  getGaClientId(store) {
    if (store.state.gaClientId !== 'clientId') {
      return store.state.gaClientId;
    }
    let clientId = 'clientId';
    if (window.ga !== undefined) {
      window.ga('create', 'UA-101279823-5', 'auto');
      window.ga(function(tracker) {
        clientId = tracker.get('clientId');
        store.commit('trackingSetGaClientId', clientId);
      });
    }
    return clientId;
  }
};

export default {
  state,
  mutations,
  actions
};
