<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="assembly-section">
        <img
          class="blue-ikea-icon"
          alt="Montageservice"
          src="./../../assets/assembly-service.svg"
        />
        <h6>Möchtest du den IKEA Montageservice nutzen?</h6>

        <form-field
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'assembly-yes',
                name: 'assembly',
                text: 'Ja',
                value: 'true'
              },
              {
                id: 'assembly-no',
                name: 'assembly',
                text: 'Nein',
                value: 'false'
              }
            ]
          }"
          :field-helper="{
            msg: 'Pro laufenden Küchenschrankmeter: <b>199.-</b>',
            link:
              '/de/de/customer-service/kuechenmontage-pub0c5a5e11',
            linkText: 'Mehr Infos',
            linkTarget: '_blank'
          }"
          :shouldValidate="startAssemblyValidate"
          :valid="isAssemblyValid"
          @change="assembly = $event;"
          :error="{
            msg: 'Bitte gib an, ob du den IKEA Montageservice nutzen möchtest.'
          }"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import { mapState } from 'vuex';

export default {
  components: {
    FormField
  },
  data() {
    return {
      assembly: null,
      startAssemblyValidate: false
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'assembly');
  },
  beforeDestroy() {
    this.$store.commit('updateAssembly', '');
    this.$store.commit('validSectionsRemove', 'assembly');
  },
  watch: {
    assembly() {
      this.$store.commit('setIsRemainingFormularTrue');
      this.$store.commit('updateAssembly', this.isAssembly);
    },
    isGlobalFormValidation() {
      this.startAssemblyValidate = true;
    },
    isAssemblyValid() {
      this.$store.commit('validSectionsUpdateSection', {
        assembly: this.isAssemblyValid
      });
    }
  },
  computed: {
    isAssembly() {
      return this.assembly === 'true';
    },
    isAssemblyValid() {
      return this.assembly !== null;
    },
    ...mapState({
      isGlobalFormValidation: state =>
        state.formularControl.isGlobalFormValidation
    })
  },
  methods: {}
};
</script>
