import axios from 'axios';
import moment from 'moment';
import _find from 'lodash/find';
import _forEach from 'lodash/forEach';
import formularData from './formularData';

const state = {
  isLoading: false,
  isError: false,
  error: '',
  authToken: '',
  authTokenInitialTime: null,
  authTokenExpiredMinutes: 10,
  timeslots: null,
  timeslot: null,
  reservation: null,
  bookedReservation: null,
  reservationTimer: '5:00',
  reservationTimerInterval: null,
  cancelBookingId: null,
  stores: null
};
const getters = {
  storeFromZipcode: state => {
    return _find(state.stores, { zip: [formularData.state.zipcode] });
  },
  isZipcodeFromSpecialStore: (state, getters) => {
    return getters.storeFromZipcode !== undefined;
  },
  hasReservation: state => {
    return state.reservation !== null;
  },
  hasBookedReservation: state => {
    return state.bookedReservation !== null;
  },
  calenderApiId: (state, getters) => {
    if (state.stores !== null) {
      if (getters.isPlanInStore && getters.storeFromZipcode !== undefined) {
        return getters.storeFromZipcode.calendarId;
      } else {
        return state.stores.online.calendarId;
      }
    } else {
      return null;
    }
  },
  isReservationTimer: state => {
    return state.reservationTimerInterval !== null;
  },
  reservationTimerText: state => {
    return (
      'Der gewählte Termin ist noch <b>' +
      state.reservationTimer +
      '</b> Minuten für dich reserviert. Solltest du innerhalb dieser Zeit das Formular nicht absenden, wird er wieder freigegeben.'
    );
  }
};
const mutations = {
  updateStores: (state, payload) => {
    // format zipcodes from string to array
    _forEach(payload, (value, key) => {
      if (value.zip !== undefined && value.zip !== null) {
        payload[key]['zip'] = value.zip.split(',').map(item => item.trim());
      }
    });
    state.stores = payload;
  },
  setLoading: (state, payload) => {
    state.isLoading = payload;
  },
  setError: (state, payload) => {
    state.isError = true;
    state.error = payload;
  },
  unsetError: () => {
    state.isError = false;
    state.error = '';
  },
  setAuthToken: (state, payload) => {
    state.authToken = payload;
  },
  setAuthTokenInitialTime: (state, payload) => {
    state.authTokenInitialTime = payload;
  },
  setTimeslots: (state, payload) => {
    state.timeslots = payload;
  },
  setTimeslot: (state, payload) => {
    state.timeslot = payload;
  },
  setBookedReservation: (state, payload) => {
    state.bookedReservation = payload;
  },
  setReservation: (state, payload) => {
    state.reservation = payload;
  },
  updateTimer: (state, payload) => {
    state.reservationTimer = payload;
  },
  stopReservationTimer: state => {
    clearInterval(state.reservationTimerInterval);
    state.reservationTimerInterval = null;
    state.reservationTimer = '5:00';
  },
  setCancelBookingId: (state, payload) => {
    state.cancelBookingId = payload;
  }
};
const actions = {
  startReservationTimer: ({ state, commit, dispatch }) => {
    const startTime = moment().format('X');
    const endTime = moment()
      .add(5, 'minutes')
      .format('X');
    const diffTime = endTime - startTime;
    let duration = moment.duration(diffTime * 1000, 'milliseconds');
    state.reservationTimerInterval = setInterval(() => {
      duration = moment.duration(duration - 1000, 'milliseconds');
      let minutes = duration.minutes();
      let seconds = duration.seconds();
      let fullSeconds = seconds + minutes * 60;
      if (fullSeconds <= 0) {
        dispatch('getAuthToken').then(() => {
          dispatch('cancel', { reservation: state.reservation });
        });
        commit('stopReservationTimer');
      } else {
        commit(
          'updateTimer',
          minutes + ':' + (seconds < 10 ? '0' + seconds : seconds)
        );
      }
    }, 1000);
  },
  getAuthToken: ({ state, commit }) => {
    if (
      state.authTokenInitialTime === null ||
      moment().diff(state.authTokenInitialTime, 'minutes') >=
        state.authTokenExpiredMinutes
    ) {
      commit('unsetError');
      commit('setLoading', true);
      return axios
        .get(
          process.env.VUE_APP_BACKEND_URL + '/api/expertencheck/getAuthToken'
        )
        .then(response => {
          commit('setAuthTokenInitialTime', moment().format());
          commit('setAuthToken', response.data.authToken);
          commit('setLoading', false);
        })
        .catch(error => {
          commit('setError', error);
          // eslint-disable-next-line
          console.error('ERROR', error);
          commit('setLoading', false);
        });
    } else {
      // cached getAuthToken
      return Promise.resolve();
    }
  },
  getFreeTimeSlotsByWeek: ({ state, commit, getters }, payload) => {
    commit('setLoading', true);
    return axios
      .get(
        process.env.VUE_APP_INWEBS_CALENDAR_API_URL + '/freeTimeSlotsByWeek',
        {
          params: {
            year: payload.year,
            calendarWeek: payload.week,
            calendar: getters.calenderApiId
          },
          headers: {
            'X-Auth-Token': state.authToken
          }
        }
      )
      .then(response => {
        commit('setTimeslots', response.data);
        commit('setLoading', false);
      })
      .catch(error => {
        commit('setError', error);
        // eslint-disable-next-line
        console.error('ERROR', error);
        commit('setLoading', false);
      });
  },
  reserve: ({ state, commit, dispatch }, payload) => {
    commit('setLoading', true);
    return axios
      .put(
        process.env.VUE_APP_INWEBS_CALENDAR_API_URL + '/reserve',
        {
          timeslot: payload.timeslot.__identity
        },
        {
          headers: {
            'X-Auth-Token': state.authToken
          }
        }
      )
      .then(response => {
        commit('setTimeslot', payload.timeslot);
        commit('setReservation', response.data);
        dispatch('startReservationTimer');
        commit('setLoading', false);
      })
      .catch(error => {
        commit('setError', error);
        // eslint-disable-next-line
        console.error('ERROR', error);
        commit('setLoading', false);
      });
  },
  book: ({ state, commit }, payloadObj) => {
    commit('setLoading', true);
    return axios.put(
      process.env.VUE_APP_INWEBS_CALENDAR_API_URL +
        '/itemset/2/book?reservation=' +
        payloadObj.query.reservation,
      payloadObj.body,
      {
        headers: {
          'X-Auth-Token': state.authToken
        }
      }
    );
  },
  cancel: ({ state, commit, getters }, payload) => {
    if (!getters.hasBookedReservation) {
      commit('setLoading', true);
      return axios
        .delete(process.env.VUE_APP_INWEBS_CALENDAR_API_URL + '/cancel', {
          params: {
            appointment: payload.reservation.RequestId
          },
          headers: {
            'X-Auth-Token': state.authToken
          }
        })
        .then(() => {
          commit('setTimeslot', null);
          commit('setReservation', null);
          commit('stopReservationTimer');
          commit('setLoading', false);
        })
        .catch(error => {
          commit('setError', error);
          commit('setTimeslot', null);
          commit('setReservation', null);
          commit('stopReservationTimer');
          // eslint-disable-next-line
          console.error('ERROR', error);
          commit('setLoading', false);
        });
    }
  },
  cancelBooking: ({ state, commit, getters }, payload) => {
    if (!getters.hasBookedReservation) {
      commit('setLoading', true);
      return axios
        .delete(process.env.VUE_APP_INWEBS_CALENDAR_API_URL + '/cancel', {
          params: {
            appointment: payload
          },
          headers: {
            'X-Auth-Token': state.authToken
          }
        })
        .then(() => {
          commit('setLoading', false);
        })
        .catch(error => {
          commit('setError', error);
          // eslint-disable-next-line
          console.error('ERROR', error);
          commit('setLoading', false);
        });
    }
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
