<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="plan-in-store-section">
        <h6>
          Wo möchtest du deine neue IKEA Küche von einem IKEA Küchenexperten
          prüfen lassen?
        </h6>

        <form-field
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'planInStore-no',
                name: 'planInStore',
                text: 'Online per Telefon',
                value: 'no'
              },
              {
                id: 'planInStore-yes',
                name: 'planInStore',
                text: 'Im IKEA Einrichtungshaus ' + storename,
                value: 'yes'
              }
            ]
          }"
          :shouldValidate="startPlanInStoreValidate"
          :valid="isPlanInStoreValid"
          @change="planInStore = $event;"
          :error="{
            msg:
              'Bitte gib an, wo du deine neue IKEA Küche prüfen lassen möchtest.'
          }"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import { mapState } from 'vuex';
import _isEmpty from 'lodash/isEmpty';

export default {
  components: {
    FormField
  },
  data() {
    return {
      planInStore: '',
      startPlanInStoreValidate: false
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'planInStore');
  },
  beforeDestroy() {
    this.$store.commit('updatePlanInStore', '');
    this.$store.commit('validSectionsRemove', 'planInStore');
  },
  watch: {
    planInStore() {
      if (this.planInStore === 'yes') {
        this.$store.commit('setIsRemainingFormularTrue');
      }
      this.$store.commit('updatePlanInStore', this.planInStore);
    },
    isGlobalFormValidation() {
      this.startPlanInStoreValidate = true;
    },
    isPlanInStoreValid() {
      this.$store.commit('validSectionsUpdateSection', {
        planInStore: this.isPlanInStoreValid
      });
    }
  },
  computed: {
    storename() {
      return this.$store.getters.storeFromZipcode.storename;
    },
    isPlanInStoreValid() {
      return !_isEmpty(this.planInStore);
    },
    ...mapState({
      isGlobalFormValidation: state =>
        state.formularControl.isGlobalFormValidation
    })
  },
  methods: {}
};
</script>
