<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="room-measurement-section">
        <img
          class="blue-ikea-icon"
          alt="Montageservice"
          src="./../../assets/room-measurement-service.svg"
        />
        <h6>
          Hast du bereits ein Raumaufmaß von IKEA vorliegen oder beauftragt?
        </h6>
        <form-field
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'roomMeasurement-yes',
                name: 'roomMeasurement',
                text: 'Ja',
                value: 'true'
              },
              {
                id: 'roomMeasurement-no',
                name: 'roomMeasurement',
                text: 'Nein',
                value: 'false'
              }
            ]
          }"
          @change="roomMeasurement = $event;"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'ordernumber',
            id: 'ordernumber',
            value: ordernumber,
            label: 'Bestellnummer (optional)',
            disabled: !isRoomMeasurement,
            maxLength: 255
          }"
          @input="ordernumber = $event;"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';

export default {
  components: {
    FormField
  },
  data() {
    return {
      roomMeasurement: null,
      ordernumber: ''
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'roomMeasurement');
    this.$store.commit('validSectionsUpdateSection', {
      roomMeasurement: true
    });
  },
  beforeDestroy() {
    this.$store.commit('validSectionsRemove', 'roomMeasurement');
  },
  watch: {
    roomMeasurement() {
      if (!this.isRoomMeasurement) {
        this.ordernumber = '';
      }
      this.$store.commit('updateRoomMeasurement', this.isRoomMeasurement);
    },
    ordernumber() {
      this.$store.commit('updateOrdernumber', this.ordernumber);
    }
  },
  computed: {
    isRoomMeasurement() {
      return this.roomMeasurement === 'true';
    }
  },
  methods: {}
};
</script>
