<template>
  <div id="app">
    <formular-page v-if="$store.state.showPage === 'formular'"></formular-page>
    <success-page v-if="$store.state.showPage === 'success'"></success-page>
    <error-page v-if="$store.state.showPage === 'error'"></error-page>
    <cancel-page v-if="$store.state.showPage === 'cancel'"></cancel-page>

    <styleyguide-svgs></styleyguide-svgs>
  </div>
</template>

<script>
import axios from 'axios';
import { mapMutations } from 'vuex';
import FormularPage from './views/Formular';
import SuccessPage from './views/Success';
import ErrorPage from './views/Error';
import CancelPage from './views/Cancel';
import StyleyguideSvgs from './components/StyleguideSvgs';

export default {
  components: {
    FormularPage,
    SuccessPage,
    ErrorPage,
    CancelPage,
    StyleyguideSvgs
  },
  created() {
    this.startLoading();
    axios
      .get(process.env.VUE_APP_BACKEND_URL + '/api/expertencheck')
      .then(response => {
        this.updateIsInstantAnswer(Number(response.data.sofort) === 1);
        this.updateStores(response.data.stores);
        this.stopLoading();
        const cancelBookingId = this.getUrlParameter('cancelAppointment');
        if (cancelBookingId !== '') {
          this.$store.commit('setCancelBookingId', cancelBookingId);
          this.$store.commit('updateShowPage', 'cancel');
        }
      })
      .catch(() => {
        this.showPage = 'error';
        this.$store.commit('updateShowPage', 'error');
      });
  },
  methods: {
    getUrlParameter(name) {
      // eslint-disable-next-line
      name = name.replace(/[\[]/, '\\[').replace(/[\]]/, '\\]');
      const regex = new RegExp('[\\?&]' + name + '=([^&#]*)');
      let results = regex.exec(location.search);
      return results === null
        ? ''
        : decodeURIComponent(results[1].replace(/\+/g, ' '));
    },
    ...mapMutations([
      'updateIsInstantAnswer',
      'updateStores',
      'startLoading',
      'stopLoading'
    ])
  }
};
</script>
