<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="contact-section">
        <div class="debug-filler" v-if="$debug">
          <div class="debug-filler-inner">
            AUTOFILL: <button @click="autofill(1);">1</button>
            <button @click="autofill(2);">2</button>
          </div>
        </div>

        <h4>Deine Kontaktdaten</h4>

        <form-field
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'title-woman',
                name: 'title',
                text: 'Frau',
                value: 'f'
              },
              {
                id: 'title-man',
                name: 'title',
                text: 'Herr',
                value: 'm'
              }
            ]
          }"
          :shouldValidate="startTitleValidate"
          :valid="isTitleValid"
          @change="title = $event;"
          :error="{ msg: 'Bitte gib deine Anrede an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'firstname',
            id: 'firstname',
            value: firstname,
            label: 'Vorname',
            maxLength: 255
          }"
          :shouldValidate="startFirstnameValidate"
          :valid="isFirstnameValid"
          @input="firstname = $event;"
          @blur="startFirstnameValidate = true;"
          :error="{ msg: 'Bitte gib deinen Vornamen an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'lastname',
            id: 'lastname',
            value: lastname,
            label: 'Nachname',
            maxLength: 255
          }"
          :shouldValidate="startLastnameValidate"
          :valid="isLastnameValid"
          @input="lastname = $event;"
          @blur="startLastnameValidate = true;"
          :error="{ msg: 'Bitte gib deinen Nachnamen an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'street',
            id: 'street',
            value: street,
            label: 'Straße',
            maxLength: 255
          }"
          :shouldValidate="startStreetValidate"
          :valid="isStreetValid"
          @input="street = $event;"
          @blur="startStreetValidate = true;"
          :error="{ msg: 'Bitte gib deine Straße an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'housenumber',
            id: 'housenumber',
            value: housenumber,
            label: 'Hausnummer',
            maxLength: 10
          }"
          :shouldValidate="startHousenumberValidate"
          :valid="isHousenumberValid"
          @input="housenumber = $event;"
          @blur="startHousenumberValidate = true;"
          :error="{ msg: 'Bitte gib deine Hausnummer an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'addressAddition',
            id: 'addressAddition',
            value: addressAddition,
            label: 'Adresszusatz (optional)',
            maxLength: 255
          }"
          @input="addressAddition = $event;"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'secondZipcode',
            id: 'secondZipcode',
            value: zipcode,
            label: 'Postleitzahl',
            placeholder: 'XXXXX',
            maxLength: 5
          }"
          :shouldValidate="startZipcodeValidate"
          :valid="isZipcodeValid"
          @input="zipcode = $event;"
          @blur="startZipcodeValidate = true;"
          :error="{ msg: 'Bitte gib deine Postleitzahl an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'city',
            id: 'city',
            value: city,
            label: 'Ort',
            maxLength: 255
          }"
          :shouldValidate="startCityValidate"
          :valid="isCityValid"
          @input="city = $event;"
          @blur="startCityValidate = true;"
          :error="{ msg: 'Bitte gib deinen Ort an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'email',
            id: 'email',
            value: email,
            label: 'E-Mail (optional)',
            maxLength: 255
          }"
          :field-helper="{
            msg: 'Falls abweichend zum Online-Login.'
          }"
          :shouldValidate="startEmailValidate"
          :valid="isEmailValid"
          @input="email = $event;"
          @blur="startEmailValidate = true;"
          :error="{ msg: 'Bitte gib eine gültige E-Mail-Adresse an.' }"
        ></form-field>

        <div class="family-loader-container">
          <form-field
            :children="{
              childType: 'input-field',
              type: 'text',
              name: 'familynumber',
              id: 'familynumber',
              value: familynumber,
              label: 'IKEA FAMILY Nr. (optional)',
              placeholder: 'XXXXXX XXXX XXXX XXXX X',
              mask: '###### #### #### #### #',
              returnMasked: false,
              maxLength: 23,
              disabled: $store.state.familynumber.loading
            }"
            :shouldValidate="startFamilynumberValidate"
            :valid="!$store.state.familynumber.hasError"
            @input="familynumber = $event;"
            @blur="checkFamilynumber"
            :error="{
              msg: familyErrorMessage
            }"
          ></form-field>
          <loading-spinner v-if="$store.state.familynumber.loading"></loading-spinner>
        </div>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'phone',
            id: 'phone',
            value: phone,
            label: 'Telefon',
            maxLength: 255
          }"
          :shouldValidate="startPhoneValidate"
          :valid="isPhoneValid"
          @input="phone = $event;"
          @blur="startPhoneValidate = true;"
          :error="{ msg: 'Bitte gib deine Telefonnummer an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'secondPhone',
            id: 'secondPhone',
            value: secondPhone,
            label: '2. Telefonnummer (optional)',
            maxLength: 255
          }"
          :shouldValidate="startSecondPhoneValidate"
          :valid="isSecondPhoneValid"
          @input="secondPhone = $event;"
          @blur="startSecondPhoneValidate = true;"
          :error="{ msg: 'Bitte gib deine Telefonnummer an.' }"
        ></form-field>

        <form-field
          class="privacy-checkbox"
          :children="{
            childType: 'checkbox',
            id: 'privacy',
            name: 'privacy',
            text:
              'Ja, ich habe die <a href=\'/de/de/customer-service/agb-datenschutzerklaerung-pub5b9b5b01\' target=\'_blank\'>Datenschutzerklärung</a> gelesen und akzeptiere diese.'
          }"
          :shouldValidate="startPrivacyValidate"
          :valid="isPrivacyValid"
          @change="updatePrivacy($event);"
          :error="{ msg: 'Bitte bestätige die Datenschutzerklärung.' }"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import LoadingSpinner from 'styleguide/packages/loading-spinner';
import { mapGetters } from 'vuex';
import _isEmpty from 'lodash/isEmpty';

export default {
  components: {
    FormField,
    LoadingSpinner
  },
  data() {
    return {
      title: '',
      startTitleValidate: false,
      firstname: '',
      startFirstnameValidate: false,
      lastname: '',
      startLastnameValidate: false,
      street: '',
      startStreetValidate: false,
      housenumber: '',
      startHousenumberValidate: false,
      addressAddition: '',
      zipcode: '',
      startZipcodeValidate: false,
      city: '',
      startCityValidate: false,
      email: '',
      startEmailValidate: false,
      familynumber: '',
      startFamilynumberValidate: false,
      phone: '',
      startPhoneValidate: false,
      secondPhone: '',
      startSecondPhoneValidate: false,
      privacyChecked: false,
      startPrivacyValidate: false
    };
  },
  mounted() {
    this.zipcode = this.getZipcode;
    this.$store.commit('validSectionsAdd', 'contact');
  },
  beforeDestroy() {
    this.$store.commit('validSectionsRemove', 'contact');
    this.$store.commit('updateTitle', '');
    this.$store.commit('updateFirstname', '');
    this.$store.commit('updateLastname', '');
    this.$store.commit('updateStreet', '');
    this.$store.commit('updateHousenumber', '');
    this.$store.commit('updateAddressAddition', '');
    this.$store.commit('updateCity', '');
    // this.$store.commit('updateZipcode', this.zipcode);
    this.$store.commit('updateEmail', '');
    this.$store.commit('updateFamilynumber', '');
    this.$store.commit('updatePhone', '');
    this.$store.commit('updateSecondPhone', '');
    this.$store.commit('updatePrivacyChecked', false);
  },
  watch: {
    isContactValid() {
      this.$store.commit('validSectionsUpdateSection', {
        contact: this.isContactValid
      });
    },
    getZipcode() {
      this.zipcode = this.getZipcode;
    },
    getIsGlobalFormValidation() {
      this.startTitleValidate = true;
      this.startFirstnameValidate = true;
      this.startLastnameValidate = true;
      this.startStreetValidate = true;
      this.startHousenumberValidate = true;
      this.startZipcodeValidate = true;
      this.startCityValidate = true;
      this.startEmailValidate = true;
      this.startFamilynumberValidate = true;
      this.startPhoneValidate = true;
      this.startSecondPhoneValidate = true;
      this.startPrivacyValidate = true;
    },
    title() {
      this.$store.commit('updateTitle', this.title);
    },
    firstname() {
      this.$store.commit('updateFirstname', this.firstname);
    },
    lastname() {
      this.$store.commit('updateLastname', this.lastname);
    },
    street() {
      this.$store.commit('updateStreet', this.street);
    },
    housenumber() {
      this.$store.commit('updateHousenumber', this.housenumber);
    },
    addressAddition() {
      this.$store.commit('updateAddressAddition', this.addressAddition);
    },
    city() {
      this.$store.commit('updateCity', this.city);
    },
    zipcode() {
      this.$store.commit('updateZipcode', this.zipcode);
    },
    email() {
      this.$store.commit('updateEmail', this.email);
    },
    familynumber() {
      this.$store.commit('resetFamilynumber');
      this.$store.commit('updateFamilynumber', this.familynumber);
    },
    phone() {
      this.$store.commit('updatePhone', this.phone);
    },
    secondPhone() {
      this.$store.commit('updateSecondPhone', this.secondPhone);
    },
    privacyChecked() {
      this.$store.commit('updatePrivacyChecked', this.privacyChecked);
    }
  },
  computed: {
    isTitleValid() {
      return !_isEmpty(this.title);
    },
    isFirstnameValid() {
      return (
        this.isNotEmptyAndHasLength(this.firstname, 255) &&
        !/^\d+$/.test(this.firstname)
      );
    },
    isLastnameValid() {
      return (
        this.isNotEmptyAndHasLength(this.lastname, 255) &&
        !/^\d+$/.test(this.lastname)
      );
    },
    isStreetValid() {
      return this.isNotEmptyAndHasLength(this.street, 255);
    },
    isHousenumberValid() {
      return this.isNotEmptyAndHasLength(this.housenumber, 255);
    },
    isZipcodeValid() {
      return (
        !_isEmpty(this.zipcode) &&
        /^\d+$/.test(this.zipcode) &&
        this.zipcode.length === 5
      );
    },
    isCityValid() {
      return (
        this.isNotEmptyAndHasLength(this.city, 255) && !/^\d+$/.test(this.city)
      );
    },
    isEmailValid() {
      return this.email.length > 0
        ? // eslint-disable-next-line
          /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.email) &&
            this.email.length <= 255
        : true;
    },
    isFamilynumberValid() {
      return this.$store.getters.isCucoReady;
    },
    familyErrorMessage() {
      if (this.$store.state.familynumber.hasError) {
        switch (this.$store.state.familynumber.error) {
          case 'notValid':
            return 'Bitte gib deine vollständige IKEA FAMILY Nummer an.';
          case 'notFound':
            return 'Wir haben deine IKEA FAMILY Nummer nicht gefunden. Bitte überprüfe sie.';
          default:
            return 'Es tut uns leid, leider gab es Probleme deine IKEA FAMILY Nummer zu überprüfen. Versuche es erneut, oder fahre ohne fort.';
        }
      } else {
        return '';
      }
    },
    isPhoneValid() {
      return (
        this.isNotEmptyAndHasLength(this.phone, 255) &&
        // eslint-disable-next-line
        /^[0-9\-+\/]+$/.test(this.phone)
      );
    },
    isSecondPhoneValid() {
      return this.secondPhone.length > 0
        ? // eslint-disable-next-line
        /^[0-9\-+\/]+$/.test(this.secondPhone) &&
            this.isNotEmptyAndHasLength(this.secondPhone, 255)
        : true;
    },
    isPrivacyValid() {
      return this.privacyChecked === true;
    },
    isContactValid() {
      return (
        this.isTitleValid &&
        this.isFirstnameValid &&
        this.isLastnameValid &&
        this.isStreetValid &&
        this.isHousenumberValid &&
        this.isZipcodeValid &&
        this.isCityValid &&
        this.isEmailValid &&
        this.isFamilynumberValid &&
        this.isPhoneValid &&
        this.isPrivacyValid
      );
    },
    ...mapGetters([
      'getIsGlobalFormValidation',
      'getformularData',
      'getZipcode'
    ])
  },
  methods: {
    checkFamilynumber() {
      this.startFamilynumberValidate = true;
      this.$store.dispatch('checkFamilynumber');
    },
    updatePrivacy(e) {
      this.privacyChecked = e.checked;
    },
    isNotEmptyAndHasLength(value, length) {
      return !_isEmpty(value) && value.length <= length;
    },
    autofill(version) {
      this.firstname = 'Jens';
      this.lastname = 'Gardeler';
      this.street = 'Schützenstraße';
      this.housenumber = '21';
      this.city = 'Hamburg';
      this.phone = '999999999';
      this.secondPhone = '888888888';
      if (version === 2) {
        this.addressAddition = '4. Stock';
        this.zipcode = '22761';
        this.email = 'jens.gardeler@gghlowe.de';
        this.familynumber = '6275980243495636526';
      }
    }
  }
};
</script>
