<template>
  <div class="success">
    <template v-if="isSubmittedTypeWithAssembly">
      <success-with-assembly></success-with-assembly>
    </template>
    <template v-else-if="isSubmittedTypeImmediately">
      <success-immediately></success-immediately>
    </template>
    <template v-else-if="isSubmittedTypeWithoutAssembly">
      <success-without-assembly></success-without-assembly>
    </template>
    <template v-else-if="isSubmittedTypeStore">
      <success-store></success-store>
    </template>
  </div>
</template>

<script>
import SuccessWithAssembly from './../components/sections/SuccessWithAssembly';
import SuccessImmediately from './../components/sections/SuccessImmediately';
import SuccessWithoutAssembly from './../components/sections/SuccessWithoutAssembly';
import SuccessStore from './../components/sections/SuccessStore';

export default {
  components: {
    SuccessWithAssembly,
    SuccessImmediately,
    SuccessWithoutAssembly,
    SuccessStore
  },
  mounted() {
    this.$store.dispatch('finalTrack');
    window.scrollTo(0, 0);
  },
  computed: {
    isSubmittedTypeWithAssembly() {
      return this.$store.state.submit.submittedType === 'WITH_ASSEMBLY';
    },
    isSubmittedTypeImmediately() {
      return this.$store.state.submit.submittedType === 'IMMEDIATELY';
    },
    isSubmittedTypeWithoutAssembly() {
      return this.$store.state.submit.submittedType === 'WITHOUT_ASSEMBLY';
    },
    isSubmittedTypeStore() {
      return this.$store.state.submit.submittedType === 'STORE';
    }
  }
};
</script>
