<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="main-section">
        <div class="debug-filler" v-if="$debug">
          <div class="debug-filler-inner">
            AUTOFILL: <button @click="autofill('12345');">1</button>
            <button @click="autofill('76777');">2</button>
          </div>
        </div>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'onlineLogin',
            id: 'onlineLogin',
            value: onlineLogin,
            label: 'Dein Online-Login',
            placeholder: 'E-Mail',
            maxLength: 255
          }"
          :field-helper="{
            msg:
              'Bitte gib deine E-Mail-Adresse an, die du beim Online-Login für den 3D-Küchenplaner verwendet hast.'
          }"
          :shouldValidate="startOnlineLoginValidate"
          :valid="isOnlineLoginValid"
          @input="onlineLogin = $event;"
          @blur="startOnlineLoginValidate = true;"
          :error="{
            msg:
              'Bitte gib deine korrekte E-Mail-Adresse für den Online-Login des 3D-Küchenplaners ein.'
          }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'planningName',
            id: 'planningName',
            value: planningName,
            label: 'Name deiner Küchenplanung',
            maxLength: 255
          }"
          :field-helper="{
            msg:
              'Bitte gib den Namen der zu prüfenden Küchenplanung aus dem 3D-Küchenplaner an.'
          }"
          :shouldValidate="startPlanningNameValidate"
          :valid="isPlanningNameValid"
          @input="planningName = $event;"
          @blur="startPlanningNameValidate = true;"
          :error="{ msg: 'Bitte gib den Namen deiner Planung an.' }"
        ></form-field>

        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'planningNumber',
            id: 'planningNumber',
            value: planningNumber,
            label: 'Planungsnummer (optional)',
            placeholder: 'XXXX-XXXX-XXXX',
            mask: '####-####-####',
            maxLength: 14
          }"
          :field-helper="{
            msg:
              'Bitte gib hier die 12-stellige Planungsnummer deiner Küchenplanung ein. <br />Du findest sie in deiner Planung unter <strong>Artikelliste und Gesamtpreis.</strong>'
          }"
          :shouldValidate="startPlanningNumberValidate"
          :valid="isPlanningNumberValid"
          @input="planningNumber = $event;"
          @blur="startPlanningNumberValidate = true;"
          :error="{
            msg: 'Bitte gib eine richtige Planungsnummer an.'
          }"
        ></form-field>

        <h6>Zur weiteren Bearbeitung benötigen wir deine Postleitzahl.</h6>
        <form-field
          :children="{
            childType: 'input-field',
            type: 'text',
            name: 'zipcode',
            id: 'zipcode',
            value: zipcode,
            label: 'Postleitzahl',
            placeholder: 'XXXXX',
            maxLength: 5
          }"
          :shouldValidate="startZipcodeValidate"
          :valid="isZipcodeValid"
          @input="zipcode = $event;"
          @blur="startZipcodeValidate = true;"
          :error="{ msg: 'Bitte gib deine Postleitzahl an.' }"
        ></form-field>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import { mapGetters } from 'vuex';
import _isEmpty from 'lodash/isEmpty';

export default {
  components: {
    FormField
  },
  data() {
    return {
      onlineLogin: '',
      startOnlineLoginValidate: false,
      planningName: '',
      planningNumber: '',
      startPlanningNameValidate: false,
      startPlanningNumberValidate: false,
      zipcode: '',
      startZipcodeValidate: false,
      initialTracked: false
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'main');
  },
  beforeDestroy() {
    this.$store.commit('validSectionsRemove', 'main');
  },
  watch: {
    onlineLogin() {
      this.initTracking();
      this.$store.commit('updateOnlineLogin', this.onlineLogin);
    },
    planningName() {
      this.initTracking();
      this.$store.commit('updatePlanningName', this.planningName);
    },
    planningNumber() {
      this.initTracking();
      this.$store.commit('updatePlanningNumber', this.planningNumber);
    },
    zipcode() {
      this.initTracking();
      this.$store.commit('updateZipcode', this.zipcode);
    },
    getZipcode() {
      this.zipcode = this.getZipcode;
    },
    getIsGlobalFormValidation() {
      this.startOnlineLoginValidate = true;
      this.startPlanningNameValidate = true;
      this.startPlanningNumberValidate = true;
      this.startZipcodeValidate = true;
    },
    isSectionsValid() {
      this.$store.commit('validSectionsUpdateSection', {
        main: this.isSectionsValid
      });
      // set isMainSectionValid true is only happened once, because the next sections should stay visible
      this.$store.commit('setIsMainSectionValidTrue');
    }
  },
  computed: {
    isOnlineLoginValid() {
      return (
        // eslint-disable-next-line
        /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(
          this.onlineLogin
        ) && this.onlineLogin.length <= 255
      );
    },
    isPlanningNameValid() {
      return !_isEmpty(this.planningName) && this.planningName.length <= 255;
    },
    isPlanningNumberValid() {
      return this.planningNumber.length > 0
        ? /\d{4}-\d{4}-\d{4}/.test(this.planningNumber)
        : true;
    },
    isZipcodeValid() {
      return (
        !_isEmpty(this.zipcode) &&
        /^\d+$/.test(this.zipcode) &&
        this.zipcode.length === 5
      );
    },
    isSectionsValid() {
      return (
        this.isOnlineLoginValid &&
        this.isPlanningNameValid &&
        this.isPlanningNumberValid &&
        this.isZipcodeValid
      );
    },
    ...mapGetters(['getIsGlobalFormValidation', 'getZipcode'])
  },
  methods: {
    initTracking() {
      if (!this.initialTracked) {
        this.$store.dispatch('initialTrack');
        this.initialTracked = true;
      }
    },
    autofill(plz) {
      this.onlineLogin = 'jens.gardeler@ggh-mullenlowe.de';
      this.planningName = 'Meine Planung 123';
      this.zipcode = plz;
      this.startOnlineLoginValidate = true;
      this.startPlanningNameValidate = true;
      this.startZipcodeValidate = true;
    },
    isNotEmptyAndHasLength(value, length) {
      return !_isEmpty(value) && value.length <= length;
    }
  }
};
</script>
