import Vue from 'vue';
import Vuex from 'vuex';
import formularControl from './modules/formularControl';
import formularData from './modules/formularData';
import calendar from './modules/calendar';
import submit from './modules/submit';
import tracking from './modules/tracking';
import familynumber from './modules/familynumber';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    showPage: 'formular',
    isInstantAnswer: false
  },
  mutations: {
    updateShowPage: (state, payload) => {
      state.showPage = payload;
    },
    updateIsInstantAnswer: (state, payload) => {
      state.isInstantAnswer = payload;
    }
  },
  actions: {},
  modules: {
    calendar,
    formularControl,
    formularData,
    submit,
    tracking,
    familynumber
  }
});
