<template>
  <div class="ikea-row">
    <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
      <div class="date-calender-section">
        <h6>Wann soll der Expertencheck stattfinden?</h6>
        <form-field
          v-if="showDesiredDate"
          :children="{
            childType: 'radio-button-group',
            list: [
              {
                id: 'desiredDate-immediately',
                name: 'desiredDate',
                text: 'Sofort',
                value: 'immediately'
              },
              {
                id: 'desiredDate-calender',
                name: 'desiredDate',
                text: 'Wunschtermin',
                value: 'calendar'
              }
            ]
          }"
          @change="desiredDate = $event;"
        ></form-field>

        <calendar></calendar>

        <div class="validate-row">
          <form-field
            :children="{}"
            :shouldValidate="startDateCalendarValidate"
            :valid="isDateCalendarValid"
            :error="{ msg: 'Bitte wähle einen Wunschtermin aus.' }"
          ></form-field>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import FormField from 'styleguide/packages/form-field';
import Calendar from './../Calendar';
import { mapGetters } from 'vuex';

export default {
  components: {
    FormField,
    Calendar
  },
  data() {
    return {
      desiredDate: null,
      startDateCalendarValidate: false
    };
  },
  mounted() {
    this.$store.commit('validSectionsAdd', 'dateCalendar');
  },
  beforeDestroy() {
    this.$store.commit('updateDesiredDate', '');
    this.$store.commit('validSectionsRemove', 'dateCalendar');
  },
  watch: {
    desiredDate() {
      this.$store.commit('updateDesiredDate', this.desiredDate);
      this.$store.commit('unsetError');
    },
    getIsGlobalFormValidation() {
      this.startDateCalendarValidate = true;
    },
    isDateCalendarValid() {
      this.$store.commit('validSectionsUpdateSection', {
        dateCalendar: this.isDateCalendarValid
      });
    }
  },
  computed: {
    showDesiredDate() {
      return (
        this.$store.state.isInstantAnswer && !this.$store.getters.isPlanInStore
      );
    },
    isDateCalendarValid() {
      return (
        this.desiredDate === 'immediately' ||
        (this.desiredDate === 'calendar' &&
          this.$store.getters.hasReservation) ||
        (this.$store.getters.isPlanInStore &&
          this.$store.getters.hasReservation) ||
        (!this.$store.state.isInstantAnswer &&
          this.$store.getters.hasReservation)
      );
    },
    ...mapGetters(['getIsGlobalFormValidation'])
  },
  methods: {}
};
</script>
