<template>
  <div class="submit-section">
    <div class="ikea-row">
      <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
        <inline-alert
          v-if="
            $store.state.submit.isSubmitted && !$store.getters.isFormularValid
          "
          type="error"
          title="Formular nicht abgesendet."
          message="Bitte überprüfe deine Angaben."
          version="subtle"
          size="m"
        ></inline-alert>

        <ikea-button
          type="filled"
          :disabled="isDisabled"
          text="Absenden"
          icon="arrow-right-24"
          icon-position="trailing"
          @click="submit"
          :loading="$store.state.submit.isLoading"
          :loadingLight="true"
        ></ikea-button>

        <inline-alert
          v-if="$store.state.submit.isError"
          type="error"
          title="Es ist ein Fehler aufgetreten."
          :message="this.$store.state.submit.error"
          icon="error"
          version="subtle"
          size="m"
        ></inline-alert>
      </div>
    </div>
  </div>
</template>

<script>
import IkeaButton from 'styleguide/packages/button';
import InlineAlert from 'styleguide/packages/inline-alert';

export default {
  components: {
    IkeaButton,
    InlineAlert
  },
  data() {
    return {};
  },
  watch: {},
  computed: {
    isDisabled() {
      return (
        !this.$store.getters.showRemainingFormular ||
        this.$store.state.familynumber.loading
      );
    }
  },
  methods: {
    submit() {
      this.$store.dispatch('submitFormular');
    }
  }
};
</script>
