import axios from 'axios';

const state = {
  familynumber: '',
  cuco: '',
  loading: false,
  error: '',
  hasError: false,
  authToken: ''
};

const getters = {
  isCucoReady(state) {
    return state.familynumber !== '' ? state.cuco !== '' : true;
  },
  familynumberValid(state) {
    return state.familynumber === '' || /\d{19}/g.test(state.familynumber);
  }
};

const mutations = {
  setFamilyLoading(state, payload) {
    state.loading = payload;
  },
  setFamilyError(state, payload) {
    state.error = payload;
    state.hasError = true;
  },
  resetFamilynumber(state) {
    state.cuco = '';
    state.error = '';
    state.hasError = false;
  },
  setFamilyAuthToken(state, payload) {
    state.authToken = payload;
  },
  setCuco(state, payload) {
    state.cuco = payload;
  },
  updateFamilynumber: (state, payload) => {
    state.familynumber = payload;
  }
};

const actions = {
  checkFamilynumber(store) {
    if (store.getters.familynumberValid && state.familynumber === '') {
      store.commit('resetFamilynumber');
    } else if (store.getters.familynumberValid && state.familynumber > 0) {
      store.commit('resetFamilynumber');
      store.commit('setFamilyLoading', true);
      store.dispatch('getFamilyAuthToken').then(() => {
        if (store.state.authToken !== '' && !store.state.hasError) {
          return axios
            .get(
              process.env.VUE_APP_MERIFOND_FAMILY_URL +
                '/RetrieveCustomerByCardNumber',
              {
                headers: {
                  auth_token: store.state.authToken,
                  'Content-Type': 'application/json'
                },
                params: {
                  cardNumber: store.state.familynumber
                }
              }
            )
            .then(response => {
              store.commit('setCuco', response.data.CuCo);
            })
            .catch(error => {
              if (error.response.status === 404) {
                store.commit('setFamilyError', 'notFound');
              } else {
                store.commit('setFamilyError', 'other');
              }
            })
            .then(() => {
              store.commit('setFamilyLoading', false);
            });
        }
      });
    } else {
      store.commit('setFamilyError', 'notValid');
    }
  },
  getFamilyAuthToken(store) {
    return axios
      .get(
        process.env.VUE_APP_BACKEND_URL +
          '/api/softkonfigurator/getFamilyAuthToken'
      )
      .then(response => {
        store.commit('setFamilyAuthToken', response.data.authToken);
      })
      .catch(() => {
        store.commit('setFamilyError', 'other');
      });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
