import axios from 'axios';
import moment from 'moment';
import formularData from './formularData';
import calendar from './calendar';
import familynumber from './familynumber';

const state = {
  isLoading: false,
  isError: false,
  error: '',
  isSubmitted: false,
  showFormError: false,
  submittedType: null,
  submittedTime: null,
  submittedEmail: null,
  submittedStore: null
};

const getters = {
  getPayload: state => {
    let id = null;
    switch (state.submittedType) {
      case 'WITH_ASSEMBLY':
        return {
          backend: {
            onlineLogin: formularData.state.onlineLogin,
            planningName: formularData.state.planningName,
            planningNumber: formularData.state.planningNumber,
            zipcode: formularData.state.zipcode,
            assembly: formularData.state.assembly,
            dateArrangementDay: formularData.state.dateArrangementDay,
            dateArrangementTime: formularData.state.dateArrangementTime,
            roomMeasurement: formularData.state.roomMeasurement,
            ordernumber: formularData.state.ordernumber,
            title: formularData.state.title,
            firstname: formularData.state.firstname,
            lastname: formularData.state.lastname,
            street: formularData.state.street,
            housenumber: formularData.state.housenumber,
            addressAddition: formularData.state.addressAddition,
            city: formularData.state.city,
            email: formularData.state.email,
            familynumber: familynumber.state.familynumber,
            phone: formularData.state.phone,
            secondPhone: formularData.state.secondPhone,
            privacyChecked: formularData.state.privacyChecked,
            submittedType: state.submittedType
          },
          api: {}
        };
      case 'IMMEDIATELY':
        return {
          backend: {
            onlineLogin: formularData.state.onlineLogin,
            planningName: formularData.state.planningName,
            planningNumber: formularData.state.planningNumber,
            zipcode: formularData.state.zipcode,
            assembly: formularData.state.assembly,
            desiredDate: formularData.state.desiredDate,
            roomMeasurement: formularData.state.roomMeasurement,
            ordernumber: formularData.state.ordernumber,
            title: formularData.state.title,
            firstname: formularData.state.firstname,
            lastname: formularData.state.lastname,
            street: formularData.state.street,
            housenumber: formularData.state.housenumber,
            addressAddition: formularData.state.addressAddition,
            city: formularData.state.city,
            email: formularData.state.email,
            familynumber: familynumber.state.familynumber,
            phone: formularData.state.phone,
            secondPhone: formularData.state.secondPhone,
            privacyChecked: formularData.state.privacyChecked,
            submittedType: state.submittedType
          },
          api: {}
        };
      case 'WITHOUT_ASSEMBLY':
        id =
          calendar.state.bookedReservation !== null
            ? calendar.state.bookedReservation.RequestId
            : null;
        return {
          backend: {
            email:
              formularData.state.email !== ''
                ? formularData.state.email
                : formularData.state.onlineLogin,
            firstname: formularData.state.firstname,
            reservation: state.submittedTime,
            requestId: id,
            submittedType: state.submittedType
          },
          api: {
            customerSalutation:
              formularData.state.title === 'm' ? 'Herr' : 'Frau',
            customerFirstName: formularData.state.firstname,
            customerLastName: formularData.state.lastname,
            comment: 'Online Termin Buchung',
            customerDeliveryAddressZip: formularData.state.zipcode,
            customerDeliveryAddressCity: formularData.state.city,
            // TODO: activate when in.webs activate in their API
            customerDeliveryAddressStreet:
              formularData.state.street + ' ' + formularData.state.housenumber,
            customerPhoneNumber: formularData.state.phone,
            customerPhoneNumber2: formularData.state.secondPhone,
            customerFamilyNumber: familynumber.state.familynumber,
            customerEmailAddress:
              formularData.state.email !== ''
                ? formularData.state.email
                : formularData.state.onlineLogin,
            customerLogin: formularData.state.onlineLogin,
            draftName: formularData.state.planningName,
            draftNumber: formularData.state.planningNumber,
            measurementOrderCode: formularData.state.ordernumber
          }
        };
      case 'STORE':
        id =
          calendar.state.bookedReservation !== null
            ? calendar.state.bookedReservation.RequestId
            : null;
        return {
          backend: {
            email:
              formularData.state.email !== ''
                ? formularData.state.email
                : formularData.state.onlineLogin,
            firstname: formularData.state.firstname,
            reservation: state.submittedTime,
            requestId: id,
            submittedType: state.submittedType,
            store: state.submittedStore
          },
          api: {
            customerSalutation: formularData.state.title,
            customerFirstName: formularData.state.firstname,
            customerLastName: formularData.state.lastname,
            comment: 'Online Termin Buchung',
            customerDeliveryAddressZip: formularData.state.zipcode,
            customerDeliveryAddressCity: formularData.state.city,
            // TODO: activate when in.webs activate in their API
            customerDeliveryAddressStreet:
              formularData.state.street + ' ' + formularData.state.housenumber,
            customerPhoneNumber: formularData.state.phone,
            customerPhoneNumber2: formularData.state.secondPhone,
            customerFamilyNumber: familynumber.state.familynumber,
            customerEmailAddress:
              formularData.state.email !== ''
                ? formularData.state.email
                : formularData.state.onlineLogin,
            customerLogin: formularData.state.customerLogin,
            draftName: formularData.state.planningName,
            draftNumber: formularData.state.planningNumber,
            measurementOrderCode: formularData.state.ordernumber
          }
        };
    }
  },
  backendUrl: state => {
    switch (state.submittedType) {
      case 'WITH_ASSEMBLY':
        return (
          process.env.VUE_APP_BACKEND_URL +
          '/api/expertencheck/submitWithAssembly'
        );
      case 'IMMEDIATELY':
        return (
          process.env.VUE_APP_BACKEND_URL +
          '/api/expertencheck/submitImmediately'
        );
      case 'WITHOUT_ASSEMBLY':
        return (
          process.env.VUE_APP_BACKEND_URL +
          '/api/expertencheck/submitWithoutAssembly'
        );
      case 'STORE':
        return (
          process.env.VUE_APP_BACKEND_URL + '/api/expertencheck/submitStore'
        );
    }
  }
};

const mutations = {
  setSubmitLoading: (state, payload) => {
    state.isLoading = payload;
  },
  setSubmitError: (state, payload) => {
    state.isError = true;
    state.error = payload;
  },
  unsetSubmitError: () => {
    state.isError = false;
    state.error = '';
  },
  setSubmittedType: (state, payload) => {
    state.submittedType = payload;
  },
  setSubmittedTime: (state, payload) => {
    state.submittedTime = payload;
  },
  setSubmittedEmail: (state, payload) => {
    state.submittedEmail = payload;
  },
  setSubmittedStore: (state, payload) => {
    state.submittedStore = payload;
  }
};

const actions = {
  submitFormular: ({ state, getters, commit, dispatch }) => {
    commit('setSubmitLoading', true);
    dispatch('startGlobalFormValidation');
    state.isSubmitted = true;
    state.showFormError = false;
    if (getters.isFormularValid) {
      // getters.getPayload;
      commit('setSubmittedType', getters.submitType);
      commit(
        'setSubmittedEmail',
        formularData.state.email !== ''
          ? formularData.state.email
          : formularData.state.onlineLogin
      );
      switch (state.submittedType) {
        case 'WITH_ASSEMBLY':
          commit(
            'setSubmittedTime',
            formularData.state.dateArrangementDay
              .replace('mo-fr', 'Montag bis Freitag')
              .replace('sa', 'Samstag') +
              ' zwischen ' +
              formularData.state.dateArrangementTime.replace('-', ' und ') +
              ' Uhr'
          );
          dispatch('sendToBackend', getters.getPayload.backend);
          break;
        case 'IMMEDIATELY':
          dispatch('sendToBackend', getters.getPayload.backend);
          break;
        case 'WITHOUT_ASSEMBLY':
        case 'STORE':
          if (state.submittedType === 'STORE') {
            commit('setSubmittedStore', getters.storeFromZipcode.storename);
          }
          dispatch('sendToApiAndBackend', {
            query: {
              reservation: calendar.state.reservation.RequestId
            },
            body: getters.getPayload.api
          });
          break;
      }
    } else {
      state.showFormError = true;
      commit('setSubmitLoading', false);
    }
  },
  sendToBackend: ({ getters, commit }, payloadObj) => {
    commit('unsetSubmitError');
    return axios
      .post(getters.backendUrl, payloadObj)
      .then(response => {
        if (response.status === 200) {
          if (response.data.error === true) {
            if (response.data.error_text === 'validation_fails') {
              commit(
                'setSubmitError',
                '<ul><li>' +
                  response.data.validation.join('</li><li>') +
                  '</li></ul>'
              );
            } else {
              commit(
                'setSubmitError',
                'Bitte versuche es später noch einmal. Wir sind bald wieder für dich da.'
              );
            }
          } else {
            commit('updateShowPage', 'success');
          }
        }
        commit('setSubmitLoading', false);
      })
      .catch(error => {
        commit(
          'setSubmitError',
          'Bitte versuche es später noch einmal. Wir sind bald wieder für dich da.'
        );
        // eslint-disable-next-line
        console.error('ERROR', error);
        commit('setSubmitLoading', false);
      });
  },
  sendToApiAndBackend: ({ commit, getters, dispatch }, payloadObj) => {
    dispatch('getAuthToken').then(() => {
      dispatch('book', payloadObj)
        .then(response => {
          commit('setBookedReservation', response.data);
          commit('stopReservationTimer');
          commit(
            'setSubmittedTime',
            moment(calendar.state.timeslot.start).format('D.M.YYYY') +
              ' um ' +
              moment(calendar.state.timeslot.start).format('HH:mm') +
              ' Uhr'
          );
          axios
            .post(getters.backendUrl, getters.getPayload.backend)
            .then(response => {
              if (response.status === 200) {
                if (response.data.error === true) {
                  if (response.data.error_text === 'validation_fails') {
                    commit(
                      'setSubmitError',
                      '<ul><li>' +
                        response.data.validation.join('</li><li>') +
                        '</li></ul>'
                    );
                  } else {
                    commit(
                      'setSubmitError',
                      'Bitte versuche es später noch einmal. Wir sind bald wieder für dich da.'
                    );
                  }
                } else {
                  commit('updateShowPage', 'success');
                }
              }
              commit('setSubmitLoading', false);
            })
            .catch(error => {
              commit(
                'setSubmitError',
                'Bitte versuche es später noch einmal. Wir sind bald wieder für dich da.'
              );
              // eslint-disable-next-line
              console.error('ERROR', error);
              commit('setSubmitLoading', false);
            });
        })
        .catch(error => {
          commit(
            'setSubmitError',
            'Bitte versuche es später noch einmal. Wir sind bald wieder für dich da.'
          );
          // eslint-disable-next-line
          console.error('ERROR', error);
          commit('setSubmitLoading', false);
        });
    });
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};
