<template>
  <div class="cancel-page ikea-container">
    <div class="ikea-row">
      <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2">
        <h1 class="text-center">Expertencheck</h1>

        <div class="center init-loading" v-if="isLoading">
          <loading-spinner></loading-spinner>
        </div>

        <inline-alert
          v-if="!isError && !isLoading"
          type="info"
          title="Dein Termin wurde storniert."
          message="Möchtest du einen neuen Termin vereinbaren, gehe hier direkt zum Expertencheck."
          version="subtle"
          size="l"
        ></inline-alert>

        <inline-alert
          v-if="isError"
          type="error"
          title="Es tut uns leid."
          message="Dein Termin wurde schon storniert oder nicht gefunden."
          version="subtle"
          size="l"
        ></inline-alert>
      </div>
    </div>

    <div class="ikea-row">
      <div class="ikea-col-sm-12 ikea-col-md-8 ikea-offset-md-2 text-center">
        <ikea-button
          v-if="!isLoading"
          class="text-center"
          type="filled"
          text="Zum Expertencheck"
          @click="goToStart();"
          icon="arrow-right-24"
          icon-position="trailing"
        ></ikea-button>
      </div>
    </div>
  </div>
</template>

<script>
import IkeaButton from 'styleguide/packages/button';
import InlineAlert from 'styleguide/packages/inline-alert';
import LoadingSpinner from 'styleguide/packages/loading-spinner';

export default {
  components: {
    IkeaButton,
    InlineAlert,
    LoadingSpinner
  },
  mounted() {
    this.cancelBookingReservation(this.$store.state.calendar.cancelBookingId);
  },
  beforeDestroy() {
    this.$store.commit('unsetError');
  },
  computed: {
    isError() {
      return this.$store.state.calendar.isError;
    },
    isLoading() {
      return this.$store.state.calendar.isLoading;
    }
  },
  methods: {
    goToStart() {
      this.$store.commit('updateShowPage', 'formular');
    },
    cancelBookingReservation(reservation) {
      this.$store.dispatch('getAuthToken').then(() => {
        if (!this.isError) {
          this.$store.dispatch('cancelBooking', reservation);
        }
      });
    }
  }
};
</script>
